import { auth, provider } from "./components/config";
import { signInWithPopup } from 'firebase/auth';
import React from 'react';
import { Button } from "antd";

function App() {
  const deviceId = "";

  const handleClick = () => {
    signInWithPopup(auth, provider).then(data => {
      console.log(data);

      var details = {
        'email': data.user.email,
        'displayName': data.user.displayName,
        'idToken': data._tokenResponse.idToken,
        'deviceId': deviceId
      };
      
      var formBody = [];
      for (var property in details) {
        var encodedKey = encodeURIComponent(property);
        var encodedValue = encodeURIComponent(details[property]);
        formBody.push(encodedKey + "=" + encodedValue);
      }
      formBody = formBody.join("&");
      
      fetch("/login_success", {
        method: 'POST',
        mode: 'cors', // defaults to same-origin
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
        },
        body: formBody
      })
    })
  }

  const deviceIdHandle = (text) => {
    deviceId = text;
  }

  return (
    <div className="App" style={{height: "100vh"}}>
      <div style={{position:"absolute", top:"50%", left:"50%", transform:"translate(-50%,-50%)"}}>
        <input placeholder="Device ID" onChange={deviceIdHandle} style={{display:"inline-block"}}/>
        <Button type="primary" shape="round" onClick={handleClick}>Google Sign-in</Button>
      </div> 
    </div>
  );
}

export default App;
